import PropTypes from 'prop-types';
import React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import MailIcon from '@mui/icons-material/Mail';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import { Avatar, useTheme } from '@mui/material';
import LightModeIcon from '@mui/icons-material/LightMode';
import me from '../Assets/me.jpg'
import { Create, DarkMode, Home, Logout, Person, Settings } from '@mui/icons-material';
import { useLocation, useNavigate } from 'react-router-dom';
import { grey, orange } from '@mui/material/colors';
const drawerWidth = 240;


function Drawerrr(props) {
  
  const currentLocaion=useLocation();
  
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const nav=useNavigate()

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const theme=useTheme();
  

  const drawer = (
    <div >
      <Toolbar >
      <IconButton aria-label="light" sx={{ml:'4.2rem'}}>
        <IconButton onClick={()=>{
          props.setmode(theme.palette.mode ==='light' ? 'dark' : 'light')
          localStorage.setItem('modetheme',theme.palette.mode=== 'dark'? 'light' : 'dark')
        }} >
          {theme.palette.mode === 'dark' ? <LightModeIcon sx={{color:orange[700]}}/> :<DarkMode sx={{color:grey[800]}} />}
        </IconButton>
      </IconButton>
      </Toolbar>
      <Divider />
      <List>
        <ListItem disablePadding sx={{bgcolor:currentLocaion.pathname==='/' ? theme.palette.bgcolor.main :null }}>
            <ListItemButton onClick={()=>{nav("/")}}>
              <ListItemIcon>
                <Home />
              </ListItemIcon>
              <ListItemText primary="Home" />
            </ListItemButton>
        </ListItem>

        <ListItem disablePadding sx={{bgcolor:currentLocaion.pathname==='/create' ? theme.palette.bgcolor.main :null }}>
            <ListItemButton onClick={()=>{nav("/create")}}>
              <ListItemIcon>
                <Create />
              </ListItemIcon>
              <ListItemText primary="Create" />
            </ListItemButton>
        </ListItem>


        <ListItem disablePadding sx={{bgcolor:currentLocaion.pathname==='/profile' ? theme.palette.bgcolor.main :null }}>
            <ListItemButton onClick={()=>{nav("/profile")}}>
              <ListItemIcon>
                <Person />
              </ListItemIcon>
              <ListItemText primary="Profile" />
            </ListItemButton>
        </ListItem>


        <ListItem disablePadding sx={{bgcolor:currentLocaion.pathname==='/settings' ? theme.palette.bgcolor.main :null }}>
            <ListItemButton onClick={()=>{nav("/settings")}}>
              <ListItemIcon>
                <Settings />
              </ListItemIcon>
              <ListItemText primary="Settings" />
            </ListItemButton>
        </ListItem>


        <ListItem disablePadding sx={{bgcolor:currentLocaion.pathname==='/logout' ? theme.palette.bgcolor.main :null }}>
            <ListItemButton onClick={()=>{nav("/logout")}}>
              <ListItemIcon>
                <Logout />
              </ListItemIcon>
              <ListItemText primary="Log out" />
            </ListItemButton>
        </ListItem>

      </List>
      <Divider />
      <List>
        {['All mail', 'Trash', 'Spam'].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: `${drawerWidth}px` },
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography sx={{flexGrow:1}} variant="h6" noWrap component="div">
            My Shop
          </Typography>
          <Avatar alt="Remy Sharp" src={me} />
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="mailbox folders"
      >
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Drawer
          container={container}
          variant="temporary"

          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },

          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box
        component="main"
        sx={{ flexGrow: 1, p: 3, width: { sm: `calc(100% - ${drawerWidth}px)` } }}
      >
        <Toolbar />
      </Box>
    </Box>
    
  );
}

Drawerrr.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default Drawerrr;