import { Route, Routes } from 'react-router-dom';
import './App.css';
import Drawerrr from './components/Drawerrr';
import Home from './components/Home';
import Createe from './Createe';
import { CssBaseline, ThemeProvider, createTheme } from '@mui/material';
import { useState } from 'react';


function App() {
  
  

  const [mode,setmode]=useState(localStorage.getItem('modetheme')===null ?'dark': 
  localStorage.getItem('modetheme')==='light'? 'light' : 'dark')
  const darkTheme = createTheme({
    palette: {
      mode,
      ...(mode === 'light'
        ? {
            // palette values for light mode
            lotuscolor:{
              main:"#42a5f5",
            },
            bgcolor:{
              main:"#e0e0e0",
            }
          }
        : {
            // palette values for dark mode
            lotuscolor:{
              main:"#1565c0",
            },
            bgcolor:{
              main:"#616161",
            }
          }),
    },
  });


  return (
    <>
    <ThemeProvider theme={darkTheme}>
      <CssBaseline />
      <Drawerrr  setmode={setmode} />
    <Routes>
      <Route path='/' element={<Home/>}/>
      <Route path='/create' element={<Createe/>}/>
    </Routes>
    </ThemeProvider>
    
    </>
  );
}

export default App;
