import styled from '@emotion/styled';
import { KeyboardArrowRight } from '@mui/icons-material';
import { Box, Button, InputAdornment, TextField } from '@mui/material'
import { blue, purple } from '@mui/material/colors';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';

const ColorButton = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText(purple[500]),
  backgroundColor: theme.palette.lotuscolor.main,
  '&:hover': {
    backgroundColor: blue[800],
  },
}));
let drawerwidth=350;

function Createe() {
  const [name,setname]=useState('')
  const [price,setprice]=useState(0)
  const [accept,setaccept]=useState(false)
  const nav=useNavigate();
  let flag=false;
  
  return (

    <Box sx={{display:'flex',justifyContent:'center',ml:{xs:'2.2rem',sm:`245px`,md:`260px`,lg:'360px',xl:'600px'},flexDirection:'column'}} component='form'>
        <Box sx={{ml:{xs:'9rem',sm:"5rem",md:`12rem`}}}>
    <Box >
    <TextField
    onChange={(e)=>
      setname(e.target.value)
    }
          error={(name ==='' && accept) ? true :false}
          label="Item"
          id="standard-start-adornment"
          sx={{ m: 1, width:{sm:'300px',md:'380px'}}}
          InputProps={{
            startAdornment: <InputAdornment sx={{boxShadow:'none'}} position="start">👉</InputAdornment>,
          }}
          variant="filled"
          // value={name}
          autoComplete="off"
        />
        {name===''&& accept && <p style={{color:'red', fontSize:'0.9rem',paddingLeft:'0.5rem' }}>This feild should not be empty</p>}
    </Box>
    </Box>
    <Box sx={{ml:{xs:'9rem',sm:"5rem",md:`12rem`}}}>
    <Box >
    <TextField
    onChange={(e)=>
      setprice(Number(e.target.value))
    }
          error={(price.value == null && accept) ? true :false}
          // fullWidth
          label="Price"
          id="standard-start-adornment"
          sx={{ m: 1, width:{sm:'300px',md:'380px'}}}
          InputProps={{
            startAdornment: <InputAdornment sx={{boxShadow:'none'}} position="start">$</InputAdornment>,
          }}
          variant="filled"
          // value={price}
          autoComplete="off"
        />
        {(price===null || price === 0)&& accept && <p style={{color:'red', fontSize:'0.9rem',paddingLeft:'0.5rem' }}>This feild should not be empty or zero</p>}
    </Box>
    </Box>
    <Box sx={{ml:{xs:'9rem',sm:"5rem", md:`12rem`},mt:'1rem'}}>
    <ColorButton variant="contained" 
    endIcon={<KeyboardArrowRight/>}
    onClick={()=>{
      if(name==='' || price.value ===null || price ===0){
        flag=true;
        setaccept(true)
      }
      if(!flag){
        fetch('http://localhost:3100/mydata',{
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            name:name,
            price:price,
          }),
        })
        nav('/')
      }
      else{
        // alert('fill all of the inputs')
      }
    }}
    >Submit</ColorButton>
    </Box>
    </Box>
    
  )
}

export default Createe