import { Close } from '@mui/icons-material'
import { Box, Paper, Typography, IconButton } from '@mui/material'
import React, { useEffect, useState } from 'react'

function Home() {
  const [getdata,setgetdata]=useState([])
  const [runuseeffect,setrunuseeffect]=useState(0)
  useEffect(()=>{
    fetch('http://localhost:3100/mydata',{
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    }).then((res)=>res.json())
    .then((data)=>setgetdata(data))
  },[runuseeffect])

  let tottalprice=0;
  const datashow=getdata.map((dataitem,index)=><>
  <Paper key={index} sx={{width:'400px',display:'flex',position:'relative',pr:'20px',pl:'20px',mb:'20px'}}>
            <Typography variant="h6"  sx={{flexGrow:'1',padding:' 30px 10px 10px 10px'}}>{dataitem.name}</Typography>
            <Typography variant="h6"  sx={{padding:' 30px 10px 10px 10px'}}>{dataitem.price}$</Typography>
            <IconButton 
            sx={{position:'absolute',right:0,top:0}}
            onClick={()=>{
              fetch(`http://localhost:3100/mydata/${dataitem.id}`,{
                method:'DELETE',
                headers: {
                  "Content-Type": "application/json",
                },
              }).then((res)=>res.json())
              .then(setrunuseeffect(1))
            }
            }
            >
              <Close />
            </IconButton>
            <p style={{display:'none'}}>{tottalprice+=dataitem.price}</p>
        </Paper>
  </>)

  return (
    <Box sx={{ml:{xs:0,sm:'220px'},display:'flex',justifyContent:'center',mt:'50px'}}>
        <Box>
        {datashow}
        <Typography variant="h6" sx={{textAlign:'center'}}>You Spent :<span style={{color:'green'}}>{tottalprice}$</span></Typography>
        </Box>
    </Box>
  )
}

export default Home